<template>
  <div class="profile">
    <van-nav-bar
      title="个人信息"
      @click-left="onGoBack"
    >
      <template #left>
        <van-icon
          name="arrow-left"
          size="16"
          color="#000"
        />
      </template>
    </van-nav-bar>
    <van-cell
      title="头像"
      is-link
    >
      <div class="image-wrapper">
        <van-image
          round
          width="20px"
          height="20px"
          :src="profile.photo"
        />
      </div>
    </van-cell>
    <van-cell
      title="昵称"
      is-link
      :value="profile.name"
    />
    <van-cell
      title="简介"
      is-link
      :value="profile.intro"
    />
    <div class="gap" />
    <van-cell
      title="性别"
      is-link
      :value="profile.gender ? '女' : '男'"
    />
    <van-cell
      title="生日"
      is-link
      :value="formatter(profile.birthday)"
    />

    <div
      class="sign-out"
      @click="onSignOut"
    >
      退出登录
    </div>
  </div>
</template>

<script>
import { Dialog } from 'vant';
import * as userApi from '@/api/user.js';
import moment from 'moment';

export default {
  data() {
    return {
      profile: {},
    };
  },
  methods: {
    // 退出登录
    onSignOut() {
      Dialog.confirm({
        title: '温馨提示',
        message: '需要退出吗？',
      })
        .then(() => {
          // 退出登录
          // 1. 清除本地存储中的用户信息
          localStorage.removeItem('token');
          // 2. 跳转到登录页面
          this.$router.push('/login');
        });
    },
    // 页面返回
    onGoBack() {
      this.$router.push('/mine');
    },
    formatter(date) {
      return moment(date).format('YYYY-MM-DD');
    },
  },
  async created() {
    const res = await userApi.getUserProfile();
    this.profile = res.data.data;
  },
};
</script>

<style lang="less" scoped>
.image-wrapper {
  display: flex;
  align-items: center;
  height: 100%;
  justify-content: flex-end;

  /deep/ .van-image {
    display: block;
  }
}

.gap {
  height: 16px;
  background: #f8f8f8;
}

.sign-out {
  position: absolute;
  bottom: 24px;
  width: 100%;
  text-align: center;
  color: orangered;
}
</style>
