<template>
  <van-list
    v-model="loading"
    :finished="finished"
    finished-text="没有更多了"
    @load="onLoad"
    class="resuts"
    offset="1"
  >
    <div
      class="article-list"
      v-for="item in data"
      :key="item.art_id"
    >
      <div
        class="acticle-item"
        v-if="item.cover.type === 1"
        @click="onArticleClick(item.art_id)"
      >
        <div
          v-if="closeable"
          @click="$emit('close')"
        >
          x
        </div>
        <div class="title">{{ item.title }}</div>
        <div class="pic">
          <img :src="item.cover.images[0]">
        </div>
        <div class="down">
          <span>{{ item.aut_name }}</span>
          <span>{{ item.collect_count }}评论</span>
          <span>{{ getYearDiff(item.pubdate) }}年内</span>
        </div>
      </div>
      <div
        class="acticle-other"
        v-else
        @click="onArticleClick(item.art_id)"
      >
        <div class="title">{{ item.title }}</div>
        <div
          class="pic"
          v-if="item.cover.type !== 0"
        >
          <img
            v-for="(img, index) in item.cover.images"
            :key="index"
            :src="img"
            alt=""
          >
        </div>
        <div class="down">
          <span>{{ item.aut_name }}</span>
          <span>{{ item.collect_count }}评论</span>
          <span>{{ getYearDiff(item.pubdate) }}年内</span>
        </div>
      </div>
    </div>
  </van-list>
</template>

<script>
import moment from 'moment';
// 节流
import { throttle } from 'lodash-es';

export default {
  props: {
    data: {
      type: Array,
      default: () => [],
    },
    req: {
      type: Function,
      default: () => () => {},
    },
    closeable: {
      type: Boolean,
      default: false,
    }
  },
  updated() {
    console.log(this.loading, 'loading');
    console.log(this.finished, 'finished');
    console.log(this.data, 'data');
  },
  data() {
    return {
      loading: true,
      finished: false,
      currentPage: 1,
    };
  },
  created() {
    this.refresh();
  },
  methods: {
    // 触底事件
    onLoad() {
      this.refresh();
      this.currentPage++;
    },
    // 获取间隔年数
    getYearDiff(date) {
      return moment().diff(date, 'years') + 1;
    },
    refresh: throttle(async function() {
      const hasNext = await this.req(this.currentPage++);
      this.loading = false;
      if (!hasNext) {
        this.finished = true;
      }
    }, 1000),
    onArticleClick(id) {
      this.$router.push({
        name: 'article',
        params: {
          q: id
        }
      });
    },
  }
};

</script>

<style lang="less" scoped>
.resuts {
  height: calc(100vh - 44px - 50px);
    overflow: auto;
}
.article-list {
  padding: 0 16px;
  .acticle-item {
    margin-top: 50px;
    display: flex;
    flex-wrap: wrap;
    border-bottom: 2px solid rgb(246, 241, 241);
    .title {
      flex: 1;
    }
    .pic {
      img {
        width: 100%;
        height: 80px;
        object-fit: cover;
      }
    }
    .down {
      width: 100%;
      color: #a6a8b3;
      font-size: 14px;
      overflow: hidden;
      display: flex;
      span {
        display: inline-block;
        margin-right: 6px;
        flex: 1;
      }
    }
  }
  .acticle-other {
    margin-top: 20px;
    margin-bottom: 16px;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    border-bottom: 2px solid rgb(246, 241, 241);
    .title {
      flex: 1;
    }
    .pic {
      margin-top: 14px;
      display: flex;
      img {
        width: 30%;
        margin-right: 14px;
      }
    }
    .down {
      width: 100%;
      margin-top: 14px;
      color: #a6a8b3;
      font-size: 14px;
      span {
        display: inline-block;
        margin-right: 6px;
        object-fit: cover;
        flex: 1;
      }
    }
  }
}
</style>


