import VueRouter from 'vue-router';
import Vue from 'vue';

// 引入页面组件
import Login from '@/pages/login';
import Home from '@/pages/home';
import Profile from '@/pages/profile';
import Search from '@/pages/search';
import SearchResult from '@/pages/search-result';
import Article from '@/pages/article';


Vue.use(VueRouter);

const routes = [
  {
    path: '/login',
    component: Login,
  },
  {
    path: '/profile',
    component: Profile,
  },
  {
    path: '/search',
    component: Search,
  },
  {
    path: '/search-result',
    component: SearchResult,
  },
  {
    path: '/article/:id',
    name: 'article',
    component: Article,
  },
  {
    path: '/',

    component: Home,
    children: [
      {
        path: '/',
        component: () => import('@/pages/home/pages/homepage'),
        name: 'Home',
      },
      {
        path: '/qa',
        component: () => import('@/pages/home/pages/qa'),
      },
      {
        path: '/video',
        component: () => import('@/pages/home/pages/video'),
      },
      {
        path: '/mine',
        component: () => import('@/pages/home/pages/mine'),
      },
    ],
  },
];

export default new VueRouter({
  routes,
});
