<template>
  <div>
    <div class="search">
      <van-icon
        class="back"
        name="arrow-left"
        @click="goBack"
      />
      <van-search
        class="search-bar"
        v-model.trim="value"
        show-action
        shape="round"
        placeholder="请输入搜索关键词"
        @input="onInput"
        @search="onSearch"
      >
        <template #action>
          <div @click="onSearch(value)">搜索</div>
        </template>
      </van-search>
    </div>
    <!-- 搜索建议展示列表 -->
    <van-cell
      v-for="option in options"
      :key="option"
      icon="search"
      @click="onSearch(option)"
    >
      <template #title>
        <div v-html="highlight(option)" />
        <!-- 'abc' >>> '<span class="highlight">a</span>bc' -->
      </template>
    </van-cell>

    <template v-if="!value">
      <van-cell
        v-if="history.length"
        center
      >
        历史记录
        <template #right-icon>
          <van-icon
            name="delete-o"
            @click="clearHistory"
          />
        </template>
      </van-cell>
      <!-- 渲染历史记录列表 -->
      <van-cell
        v-for="item in history.reverse()"
        :key="item"
        center
        @click="onSearch(item)"
      >
        {{ item }}
        <template #right-icon>
          <van-icon
            name="cross"
            @click="removeHistory(item)"
          />
        </template>
      </van-cell>
    </template>
  </div>
</template>

<script>
import { Search } from 'vant';
import * as searchApi from '@/api/search';
// 防抖
import { debounce } from 'lodash-es';

export default {
  components: {
    [Search.name]: Search,
  },
  data() {
    return {
      value: '',
      options: [],
      history: JSON.parse(localStorage.getItem('history')) || [],
    };
  },
  methods: {
    goBack() {
      this.$router.go(-1);
    },
    // 点击搜索按钮
    onSearch(value) {
      // 先取历史记录
      const history = JSON.parse(localStorage.getItem('history')) || [];
      // 如果没有value 就推入一个新项 并且保存
      if (!history.includes(value)) {
        history.push(value);
        localStorage.setItem('history', JSON.stringify(history));
      }
      this.$router.push({
        path: '/search-result',
        // query会拼在问号后面
        // 例: /search-result?q=aaa
        query: {
          q: value,
        },
      });
    },
    onInput: debounce(async function onInput(value) {
      // 空字符串不请求
      if (!value) {
        this.options = [];
        return;
      }
      const res = await searchApi.getSearchSuggest(value);
      // 不是字符串的筛选掉
      this.options = res.data.data.options.filter(op => typeof op === 'string');
    }, 300),
    // 高亮
    highlight(str) {
      return str.toLowerCase().replace(this.value, `<span class="highlight">${ this.value }</span>`);
    },
    // 移除历史记录
    removeHistory(item) {
      // 从渲染数组中移除
      this.history = this.history.filter(his => his !== item);
      // 更新至本地存储
      localStorage.setItem('history', JSON.stringify(this.history));
    },
    // 清空历史记录
    clearHistory() {
      // 从渲染数组中移除
      this.history = [];
      // 更新至本地存储
      localStorage.setItem('history', JSON.stringify(this.history));
    },
  },
};
</script>

<style lang="less" scoped>
.search {
  display: flex;
  .back {
    line-height: 54px;
  }
  .search-bar {
    flex: 1;
  }
}
.back-btn {
  margin-right: 8px;
}

/deep/ .highlight {
  color: @primary;
}

</style>
