import req from '@/utils/request';

// 获取搜索建议
export function getSearchSuggest(value) {
  return req({
    url: '/suggestion',
    params: {
      q: value,
    },
  });
}

// 获取搜索结果
export function getSearchResult(currentPage, keyword) {
  return req({
    url: '/search',
    params: {
      page: currentPage,
      per_page: 10,
      q: keyword,
    },
  });
}

// 获取新闻详情
export function getArticleDetail(article_id) {
  return req({
    url: `/articles/${article_id}`,
  });
}


// 获取文章新闻推荐
export function getChannelArticleList(channel_id, timestamp) {
  return req({
    url: '/articles',
    params: {
      channel_id,
      timestamp,
    },
  });
}
