import req from '@/utils/request';

// 获取评论
export function getComment(isArticle, sourceId) {
  return req({
    url: '/comments',
    params: {
      type: isArticle ? 'a' : 'c',
      source: sourceId,
    },
  });
}
// 评论文章 发表评论
export function commentArticle(target, content) {
  return req({
    url: '/comments',
    method: 'post',
    data: {
      target,
      content,
    },
  });
}

// 对文章点赞
export function likeArticle(id) {
  return req({
    url: '/article/likings',
    method: 'post',
    data: {
      target: id,
    },
  });
}
// 取消对文章点赞
export function unlikeArticle(id) {
  return req({
    url: `/article/likings/${id}`,
    method: 'delete',
  });
}

// 回复评论
export function replyComment(id) {
  return req({
    url: '/comment/likings',
    method: 'post',
    data: {
      target: id,
    },
  });
}
