<template>
  <div>
    <van-nav-bar
      left-arrow
      @click-left="goBack"
      fixed
    >
      <template #right>
        <van-icon
          name="more-o"
          size="18"
          @click="onPopupOpen"
        />
      </template>
    </van-nav-bar>

    <!-- 分享弹出框 -->
    <van-share-sheet
      v-model="showShare"
      title="立即分享给好友"
      :options="options"
    />
    <!-- 文章头部 -->
    <div class="artcle-list">
      <h4>{{ results.title }}</h4>
      <div class="down">
        <span>{{ getDate(results.pubdate) }} 丨</span>
        <span>{{ results.read_count }}阅读 丨</span>
        <span>{{ results.comm_count }}评论</span>
      </div>
      <!-- 吸顶后显示内容 -->
      <div>
        <van-sticky>
          <div
            class="info"
          >
            <van-image
              round
              width="3rem"
              height="3rem"
              :src="results.aut_photo"
            />
            <div class="author">{{ results.aut_name }}</div>
            <van-button
              round
              :type="results.is_followed ? 'info' : 'primary'"
              size="mini"
              class="follow-btn"
              @click="onCurrentCommentFollow(currentComment)"
            >
              {{ results.is_followed ? '+关注' : '已关注' }}
            </van-button>
          </div>
        </van-sticky>

        <div
          class="artcle-content"
          v-html="results.content"
        />
        <div class="later">
          <span>发布文章时间:</span>
          <span>{{ results.pubdate }}</span>
        </div>
        <!-- 全部评论 -->
        <div class="comment-top">
          <span>全部评论({{ CommentList.total_count }})</span>
          <span>1 点赞</span>
        </div>
        <!-- 封装的评论组件 -->
        <comment-list
          :data="CommentList"
          :loading="commentListLoading"
          @reply="onCommentReply"
          @like="onCommentLike"
        />
      </div>
      <!-- 底部 -->
      <div class="artcle-down">
        <van-field
          class="input"
          placeholder="抢沙发"
          @click="onComment"
        />
        <div class="more">
          <van-icon
            name="chat-o"
            badge="9"
            size="25px"
          />
          <van-icon
            :color="results.like_count === 1 ? 'red' : ''"
            name="good-job-o"
            size="25px"
            @click="onLike"
          />
          <van-icon
            :color="results.like_count === 1 ? 'red' : ''"
            name="like-o"
            size="25px"
          />
          <van-icon
            name="share-o"
            size="25px"
          />
        </div>
      </div>
    </div>
    <!-- 评论弹出层 -->
    <van-popup
      v-model="isShow"
      position="bottom"
      :style="{ height: '100vh', width: '100%' }"
    >
      <van-nav-bar
        title="评论文章"
        right-text="发表"
        left-arrow
        @click-left="onClickLeft"
        @click-right="onCommentArticle($route.params.q, message)"
      />
      <van-field
        class="input-box"
        v-model="message"
        rows="5"
        autosize
        type="textarea"
        placeholder="说点什么..."
      />
    </van-popup>
    <!-- 回复评论弹出层 -->
    <van-popup
      v-model="isShowCommentPopup"
      position="right"
      :style="{ height: '100vh', width: '100%' }"
    >
      <van-nav-bar
        title="0条回复"
        left-arrow
        @click-left="isShowCommentPopup = false"
      />
      <!-- 当前评论 -->
      <div
        class="current-comment"
      >
        <van-image
          round
          width="3rem"
          height="3rem"
          :src="results.aut_photo"
          class="avatar"
        />
        <div class="comment-content">
          <header>
            <div class="username">2324523</div>
            <van-button
              round
              :type="results.is_followed ? 'info' : 'primary'"
              size="mini"
              class="follow-btn"
              @click="onCurrentCommentFollow(currentComment)"
            >
              {{ results.is_followed ? '+关注' : '已关注' }}
            </van-button>
          </header>

          <div class="content">内容</div>
          <footer>
            <span class="comment-time">1小时内</span>
            <span class="like-count">2</span>
          </footer>
        </div>
      </div>
    </van-popup>
  </div>
</template>

<script>
import * as searchApi from '@/api/search';
import * as commentApi from '@/api/comment';
import moment from 'moment';
import withGoBack from '@/mixins/with-go-back';
import * as userApi from '@/api/user';
import CommentList from '@/components/comment-list';
import 'highlight.js/styles/monokai.css';

export default {
  mixins: [withGoBack],
  components: {
    CommentList,
  },
  data() {
    return {
      message: '',
      active: -1,
      results: {},
      CommentList: [],
      currentComment: {},
      // 评论弹窗是否显示
      commentListLoading: true,
      showShare: false,
      isShow: false,
      isShowCommentPopup: false,
      options: [
        [
          { name: '微信', icon: 'wechat' },
          { name: '朋友圈', icon: 'wechat-moments' },
          { name: '微博', icon: 'weibo' },
          { name: 'QQ', icon: 'qq' },
        ],
        [
          { name: '复制链接', icon: 'link' },
          { name: '分享海报', icon: 'poster' },
          { name: '二维码', icon: 'qrcode' },
          { name: '小程序码', icon: 'weapp-qrcode' },
        ],
      ],
    };
  },
  async created() {
    this.refresh();
    this.commentListLoading = false;
  },
  methods: {
    onPen(item) {
      console.log(item.com_id);
    },
    onPopupOpen() {
      this.showShare = true;
    },
    getDate(date) {
      return moment(date).diff(date, 'year') + 1;
    },
    onComment() {
      this.isShow = true;
    },
    onClickLeft() {
      this.$router.go(-1);
    },
    // 回复评论
    onCommentReply(item) {
      console.log('评论', item);
      this.isShowCommentPopup = true;
    },
    onCommentLike() {
      console.log('点赞', item);
    },
    onCommentArticle(target, content) {
      commentApi.commentArticle(target, content)
        .then((res) => {
          this.message = '';
          this.isShow = false;
          this.refresh();
          console.log(res);
          console.log('评论成功');
        });
      this.refresh();
    },
    onLike() {
      if (this.results.like_count === 1) {
        commentApi.unlikeArticle(this.results.art_id)
          .then((res) => {
            console.log(res);
            console.log('取消点赞');
            this.refresh();
          });
      } else {
        commentApi.likeArticle(this.results.art_id)
          .then((res) => {
            console.log(res);
            console.log('点赞成功');
            this.refresh();
          });
      }
    },
    // 刷新数据
    async refresh() {
      const res = await searchApi.getArticleDetail(this.$route.params.q);
      this.results = res.data.data;
      console.log(this.results);
      commentApi.getComment(true, this.$route.params.q)
        .then((res) => {
          console.log(res);
          this.CommentList = res.data.data.results;
        });
    },
    onFonCurrentCommentFollowollow(currentItem) {
      // const is_followed = this.results.is_followed;
      userApi[currentItem ? 'unFollow' : 'Follow'](currentItem.aut_id)
        .then(async() => {
          console.log(is_followed ? '关注成功' : '取消关注成功');
          await this.refresh();
          this.currentComment = this.CommentList.find(item => item.com_id === currentItem.com_id);
        });
    },
  },
};
</script>

<style lang="less" scoped>
.input-box {
  width: 100%;
  height: 200px;
  background-color: #eee;
  border-radius: 20px;
  margin-top: 20px;
}
.artcle-list {
  height: calc(100vh - 100px);
  padding: 0 18px;
  h4 {
    font-size: 24px;
    font-weight: 600;
    margin-bottom: 10px;
  }
  .down {
    display: flex;
    align-items: center;
    span {
      margin-right: 10px;
      font-size: 12px;
      color: #999;
    }
  }
}
.info {
  display: flex;
  align-items: center;
  padding: 10px 10px;
  border-bottom: 2px solid rgb(247, 244, 244);
  span {
    margin-right: 8px;
  }
  .author {
    flex: 1;
    margin-left: 14px;
  }
  .follow-btn {
    width: 50px;
    height: 30px;
    // background-color: #fc6627;
    color: #fff;
    border: none;
  }
}


.other {
  height: 40px;
}
.artcle-content {
  overflow: auto;
}
.artcle-down {
  padding: 0 10px;
  width: 100%;
  display: flex;
  position: fixed;
  bottom: 0;
  background-color: #fff;
  .input {
    width: 180px;
    height: 30px;
    line-height: 10px;
    margin-left: 10px;
    margin-top: 3px;
    background-color: #f3f4f6;
    border-radius: 20px;
    }
  .more {
    flex: 1;
    align-items: center;
    margin-top: 10px;
    .van-icon {
      margin-left: 15px;
    }
  }
}
// 回复评论顶部样式
.current-comment {
  display: flex;
  padding: 16px;
  border-bottom: 1px solid @bg;

  .avatar {
    margin-right: 8px;
  }

  .comment-content {
    flex: 1;

    header {
      display: flex;
      align-items: center;

      .username {
        margin-right: 8px;
        flex: 1;
      }

      .follow {
        font-size: 12px;
        color: @primary;
      }

    }

    .content {
      word-break: break-all;
      margin-top: 8px;
      color: #666;
    }

    footer {
      margin-top: 8px;
      display: flex;
      align-items: center;

      .like-count {
        margin-right: 4px;
        font-size: 12px;
      }

      .comment-time {
        flex: 1;
        color: @text-gray;
        font-size: 12px;
      }
    }
  }
}

</style>
