import req from '@/utils/request';

// 登录
export function login(data) {
  return req({
    url: '/authorizations',
    method: 'post',
    data,
  });
}

// 获取用户信息
export function getUserInfo() {
  return req({
    url: '/user',
  });
}

// 获取用户个人资料
export function getUserProfile() {
  return req({
    url: '/user/profile',
  });
}

// 关注用户
export function Follow(userId) {
  return req({
    url: '/user/followings',
    method: 'post',
    // body的值写入data
    data: {
      target: userId,
    },
  });
}

// 取消关注用户
export function unFollow(userId) {
  return req({
    url: `/user/followings/${userId}`,
    method: 'delete',
  });
}
