<template>
  <div>
    <van-nav-bar
      title="搜索结果"
      left-arrow
      @click-left="onBack"
    />
    <article-list
      :data="results"
      :req="onReq"
      :closeable="true"
      @close="onCloseList"
    />
  </div>
</template>

<script>
import * as articleApi from '@/api/search';
import ArticleList from '@/components/article-list';

export default {
  components: {
    ArticleList,
  },
  data() {
    return {
      results: [],
    };
  },
  created() {
  },
  methods: {
    onBack() {
      this.$router.go(-1);
    },
    async onReq(currentPage) {
      const res = await articleApi.getSearchResult(currentPage, this.$route.query.q);
      this.results = this.results.concat(res.data.data.results);
      return res.data.data.results.length === 20;
    },

    // 节流 lodash-es
    // 装饰着模式refresh >>> 被节流的refresh
    onCloseList() {
      console.log(13);
    }
  },
};
</script>

